.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: #f5f5f5;
}

.Credentials{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100vw;
  background-color: #f5f5f5;
}

.Credentials div{
  padding: 0.5em;
}

.EngineSelector {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100vw;
  background-color: #f5f5f5;
  padding: 0.5rem;
}

.ChatContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  background-color: #f5f5f5;
}

.HumanMessage {
  display: flex;
  flex-direction: row;
  align-items: right;
  justify-content: flex-end;
  width: 20rem;
  margin: 0.5rem;
  margin-left: 5rem;
}

.HumanMessageText {
  display: flex;
  flex-direction: row;
  align-items: right;
  justify-content: flex-end;
  background-color: #00ff00;
  border-radius: 0.5rem;
  padding: 0.5rem;
}

.RobotMessage {
  display: flex;
  flex-direction: row;
  align-items: left;
  justify-content: flex-start;
  width: 20rem;
  margin: 0.5rem;
  margin-right: 5rem;
}

.RobotMessageText {
  display: flex;
  flex-direction: row;
  align-items: left;
  justify-content: flex-start;
  background-color: #808080;
  color: #ffffff;
  border-radius: 0.5rem;
  padding: 0.5rem;
}

.UserInput textarea {
  height: 5rem;
  width: 25rem;
}
